export const drawPasswordValidator = [
  { required: true, message: '', trigger: 'blur'},
  { validator: (rule, value, callback) => {
    if (value === '') {
      callback(new Error(''));
    } else {
      if (!/^\d+$/.test(value)) {
        callback(new Error('6'));
      }else {
        if(value.length != 6) {
          callback(new Error('6'));
        }
        callback();
      }
    }
  }, trigger: 'blur' },
]

